*,
::after,
::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-family: "Gilroy", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #002364;
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 1.4;
  background: #fff;
  scroll-behavior: smooth;
}
@media (max-width: 1280px) {
  body {
    font-size: 18px;
    font-size: 1.125rem;
  }
}
@media (max-width: 599px) {
  body {
    font-size: 16px;
    font-size: 1rem;
  }
}

input::-ms-clear {
  display: none;
}

/* Для Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 28px;
}
@media (max-width: 1140px) {
  p {
    margin: 0 0 24px;
  }
}
@media (max-width: 480px) {
  p {
    margin: 0 0 22px;
  }
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Gilroy";
  font-weight: 500;
  color: #002364;
  margin: 40px 0px 40px;
  text-transform: none;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
    margin: 24px 0 16px;
  }
}

h1, .h1 {
  font-size: 62px;
  font-size: 3.875rem;
  margin-top: 0;
  line-height: 1;
}
@media (max-width: 1280px) {
  h1, .h1 {
    font-size: 56px;
    font-size: 3.5rem;
  }
}
@media (max-width: 1140px) {
  h1, .h1 {
    font-size: 50px;
    font-size: 3.125rem;
  }
}
@media (max-width: 959px) {
  h1, .h1 {
    font-size: 46px;
    font-size: 2.875rem;
  }
}
@media (max-width: 599px) {
  h1, .h1 {
    font-size: 36px;
    font-size: 2.25rem;
    line-height: 1.14;
  }
}
@media (max-width: 480px) {
  h1, .h1 {
    font-size: 28px;
    font-size: 1.75rem;
  }
}

h2, .h2, .ui-h2 {
  font-size: 48px;
  font-size: 3rem;
  line-height: 1.2;
}
@media (max-width: 1280px) {
  h2, .h2, .ui-h2 {
    font-size: 44px;
    font-size: 2.75rem;
  }
}
@media (max-width: 1140px) {
  h2, .h2, .ui-h2 {
    font-size: 42px;
    font-size: 2.625rem;
  }
}
@media (max-width: 959px) {
  h2, .h2, .ui-h2 {
    font-size: 40px;
    font-size: 2.5rem;
  }
}
@media (max-width: 599px) {
  h2, .h2, .ui-h2 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  h2, .h2, .ui-h2 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

h3, .h3, .ui-h3 {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h3, .h3, .ui-h3 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 599px) {
  h3, .h3, .ui-h3 {
    font-size: 26px;
    font-size: 1.625rem;
  }
}
@media (max-width: 480px) {
  h3, .h3, .ui-h3 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

h4, .h4, .ui-h4 {
  font-size: 30px;
  font-size: 1.875rem;
}

h5, .h5 {
  font-size: 24px;
  font-size: 1.5rem;
}

h6, .h6, .ui-h5 {
  font-size: 20px;
  font-size: 1.25rem;
  text-transform: none;
  font-weight: 700;
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  color: #0077C8;
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px solid #C5D5F5;
  overflow: hidden;
}

table {
  border-collapse: collapse;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

/*---- ICONS ---- */
::selection {
  background: #fff;
  color: #FFF;
}

::-moz-selection {
  background: #fff;
  color: #FFF;
}

@media (min-width: 992px) {
  ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #FFCC00;
  }

  ::-webkit-scrollbar-thumb {
    width: 6px;
    height: 6px;
    background-color: #0077C8;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #008ff1;
  }
}
.color-red {
  color: #F00;
}

.ui-tabs {
  white-space: nowrap;
  overflow-x: auto;
}

.ui-tab {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 500;
  text-transform: uppercase;
  display: inline-block;
  vertical-align: baseline;
  margin-right: 52px;
  cursor: pointer;
  transition: color 0.3s;
}
@media (max-width: 1280px) {
  .ui-tab {
    font-size: 22px;
    font-size: 1.375rem;
    margin-right: 40px;
  }
}
@media (max-width: 1024px) {
  .ui-tab {
    font-size: 20px;
    font-size: 1.25rem;
    margin-right: 36px;
  }
}
@media (max-width: 480px) {
  .ui-tab {
    font-size: 16px;
    font-size: 1rem;
    margin-right: 28px;
    line-height: normal;
  }
}
.ui-tab:last-child {
  margin-right: 0;
}
.ui-tab.active {
  border-bottom: 4px solid #FFCC00;
}
@media (max-width: 599px) {
  .ui-tab.active {
    border-width: 2px;
  }
}
.ui-tab:hover {
  color: #0077C8;
}

.text-uppercase {
  text-transform: uppercase;
}

.section-title {
  text-transform: uppercase;
  font-weight: 700;
}

.ui-tabs-wrapper {
  transition: transform 0.3s;
  transform: scale(0.1);
}
.ui-tabs-wrapper:not(.active) {
  height: 0;
  overflow: hidden;
  display: block;
}
.ui-tabs-wrapper.active {
  transform: scale(1);
}

.ui-btn {
  letter-spacing: 1.32px;
  letter-spacing: 0.0825rem;
  font-weight: 500;
  line-height: normal;
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 10px;
  border: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background-color: #0077C8;
  color: #fff;
  text-align: center;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
  transition: background-color 0.2s;
}
@media (max-width: 1280px) {
  .ui-btn {
    padding-top: 24px;
    padding-bottom: 24px;
    padding-left: 36px;
    padding-right: 36px;
  }
}
@media (max-width: 992px) {
  .ui-btn {
    font-weight: 600;
  }
}
@media (max-width: 480px) {
  .ui-btn {
    padding-top: 22px;
    padding-bottom: 22px;
    border-radius: 8px;
  }
}
.ui-btn-icon {
  width: 16px;
  height: 16px;
  margin-left: 12px;
  display: inline-block;
  vertical-align: middle;
}
@media (max-width: 1280px) {
  .ui-btn-icon {
    width: 14px;
    height: 14px;
  }
}
@media (max-width: 599px) {
  .ui-btn-icon {
    width: 12px;
    height: 12px;
  }
}
.ui-btn-block {
  display: block;
  width: 100%;
}
.ui-btn-rounded {
  letter-spacing: 0.3px;
  letter-spacing: 0.01875rem;
  border-radius: 40px;
  padding-left: 48px;
  padding-right: 48px;
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (max-width: 480px) {
  .ui-btn-rounded {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.ui-btn:hover {
  background-color: #0061A3;
}
.ui-btn:active {
  background-color: #0061A3;
}

.ui-field {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 28px;
  position: relative;
}
@media (max-width: 1280px) {
  .ui-field {
    gap: 6px;
    margin-bottom: 22px;
  }
}
@media (max-width: 480px) {
  .ui-field {
    gap: 4px;
    margin-bottom: 16px;
  }
}
.ui-field__agree {
  margin-bottom: 58px;
}
@media (max-width: 1280px) {
  .ui-field__agree {
    margin-bottom: 50px;
  }
}
@media (max-width: 768px) {
  .ui-field__agree {
    margin-bottom: 40px;
  }
}
@media (max-width: 480px) {
  .ui-field__agree {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
}
.ui-field label.error {
  font-size: 16px;
  font-size: 1rem;
  color: #FF0000;
  position: absolute;
  bottom: 0;
  left: 20px;
  z-index: 1;
}
@media (max-width: 599px) {
  .ui-field label.error {
    font-size: 12px;
    font-size: 0.75rem;
    left: 12px;
  }
}

.ui-label {
  font-size: 16px;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media (max-width: 599px) {
  .ui-label {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media (max-width: 480px) {
  .ui-label {
    font-size: 12px;
    font-size: 0.75rem;
  }
}
.ui-label-agree {
  vertical-align: middle;
  display: inline-block;
  line-height: normal;
}
.ui-label-required {
  font-size: 18px;
  font-size: 1.125rem;
}
@media (max-width: 599px) {
  .ui-label-required {
    font-size: 17px;
    font-size: 1.0625rem;
  }
}
@media (max-width: 480px) {
  .ui-label-required {
    font-size: 16px;
    font-size: 1rem;
  }
}

.ui-input {
  font-size: 18px;
  font-size: 1.125rem;
  color: #002364;
  line-height: normal;
  padding: 30px 20px;
  border-radius: 10px;
  border: 1px solid #C5D5F5;
  background-color: #E5EEFF;
  outline: none;
}
@media (max-width: 1280px) {
  .ui-input {
    padding: 22px 18px;
  }
}
@media (max-width: 480px) {
  .ui-input {
    font-size: 17px;
    font-size: 1.0625rem;
    padding: 16px 12px;
    border-radius: 8px;
  }
}
.ui-input.error {
  border-color: #FF0000;
}

.ui-select.opened .jq-selectbox__trigger {
  transform: rotate(180deg);
}
.ui-select.error ~ .jq-selectbox__select {
  border-color: #FF0000;
}
.ui-select .jq-selectbox__select {
  font-size: 18px;
  font-size: 1.125rem;
  color: #002364;
  padding: 30px 20px;
  border-radius: 10px;
  border: 1px solid #C5D5F5;
  background-color: #E5EEFF;
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1280px) {
  .ui-select .jq-selectbox__select {
    padding: 22px 18px;
  }
}
@media (max-width: 480px) {
  .ui-select .jq-selectbox__select {
    font-size: 17px;
    font-size: 1.0625rem;
    padding: 16px 12px;
