  padding-top: 42px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 54px;
  background-color: #fff;
}
@media (max-width: 1280px) {
  .ui-select .jq-selectbox__dropdown {
    padding-top: 34px;
    padding-bottom: 34px;
  }
}
@media (max-width: 992px) {
  .ui-select .jq-selectbox__dropdown {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}
@media (max-width: 480px) {
  .ui-select .jq-selectbox__dropdown {
    padding: 26px 20px;
  }
}
.ui-select .jq-selectbox__dropdown li {
  cursor: pointer;
  transition: color 0.3s;
}
.ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
  margin-bottom: 28px;
}
@media (max-width: 1280px) {
  .ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
    margin-bottom: 20px;
  }
}
@media (max-width: 992px) {
  .ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
    margin-bottom: 18px;
  }
}
@media (max-width: 768px) {
  .ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
    margin-bottom: 16px;
  }
}
@media (max-width: 480px) {
  .ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
    margin-bottom: 14px;
  }
}
.ui-select .jq-selectbox__dropdown li.disabled {
  height: 0;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
}
.ui-select .jq-selectbox__dropdown li.selected {
  color: #C5D5F5;
}
.ui-select .jq-selectbox__dropdown li:hover {
  color: #0077C8;
}
.ui-select .jq-selectbox__trigger {
  background-image: url(../images/svg/chevron-down.svg);
  background-repeat: no-repeat;
  width: 12px;
  height: 8px;
  background-size: 12px 8px;
  transition: transform 0.3s;
}

.ui-checkbox {
  width: 28px;
  height: 28px;
  border-radius: 2px;
  border: 1px solid #C5D5F5;
  background-color: #E5EEFF;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
